/*Map*/
.leaflet-map {
    height: 350px;
    width: 100%;
}
/*table*/
table {
    width: 100%;
    margin-bottom: 1.5rem;
    border-collapse: collapse;
    vertical-align: middle;
    td,
    th {
        padding: 10px 20px;
        border: 1px solid #eee;
        vertical-align: middle;
    }
    thead > tr > th {
        vertical-align: middle;
        border-bottom: 0;
    }
    p {
        margin-bottom: 0;
    }
    &.clean {
        td,
        th {
            border: 0;
            border-top: 1px solid #eee;
        }
    }
    .product-thumbnail img {
        max-width: 80px;
    }
}
/*divider*/
.divider {
    position: relative;
    overflow: hidden;
    height: 4px;
    z-index: 9;

    &.center_icon {
        text-align: center;
        height: auto;
    }
    &::before,
    &::after {
        content: "";
        position: absolute;
        width: 100%;
        top: 50%;
        left: 0px;
        height: 0;
        border-top: 1px solid #eee;
        -moz-transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    &::before {
        margin-top: -1px;
    }
    &::after {
        margin-top: 1px;
    }
    i {
        background-color: #fff;
        color: #aaa;
        position: relative;
        z-index: 1;
        font-size: 20px;
        padding: 0 20px;
        line-height: 1;
    }
}

.social-network {
    .social-icon {
        svg {
            height: 14px;
            fill: #fff;
            margin: 0 5px;
        }
        display: flex;
        align-items: center;
        .line-right {
            border-right: 1px solid #fff;
        }
    }
}

.social-icons.social-icons-colored-hover {
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
        height: 14px;
        fill: #c1c7cf !important;
    }
    li:hover {
        svg {
            fill: #000 !important;
        }
    }
    strong {
        margin-top: 5px;
        display: block;
    }
}
