/*BLOG*/
.post-list {
    article:not(:last-child) {
        margin-bottom: 20px;
    }
    .post-thumb {
        max-width: 200px;
        overflow: hidden;
        a {
            margin-bottom: 0;
        }
    }
    .post-title {
        font-weight: 500;
        a {
            color: $color-grey-1;
        }
    }
}
.single-content {    
    text-rendering: optimizeLegibility;

    h1{font-size:56px;line-height:72px;margin-bottom: 32px;}
    h2{font-size:48px;line-height:64px;margin-bottom: 30px;}
    h3{font-size:36px;line-height:48px;margin-bottom: 28px;}
    h4{font-size:24px;line-height:32px;margin-bottom: 26px;}
    h5{font-size:20px;line-height:24px;margin-bottom: 24px;}
    h6{font-size:14px;line-height:24px;letter-spacing:0.5px;text-transform:uppercase;margin-bottom: 22px;}
    p{margin-bottom:1.2em;font-weight:400;}

    ul {
        list-style: circle;
        padding-left: 1rem;
        li {
            position: relative;
            margin-bottom: 7px;
        }
    }



}
.single-header {
    margin-bottom: 30px;
    &.style-2 {
        border-bottom: 1px solid #eee;
        padding-bottom: 10px;
    }
	.entry-meta.meta-1 {
		display: block;
	}
	.single-header-meta {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
.single-thumbnail {
    margin-bottom: 30px;
    img{
        border-radius: 10px;
        overflow: hidden;
    }
}
.entry-bottom {
	border-top: 1px solid #eee;
	padding-top: 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
/*Entry meta*/

.entry-meta {
	line-height: 1;
	&.meta-2 {
		.author-img {
			img {
				width: 40px;
				height: 40px;
				border-radius: 50%;
			}
		}
		.author-name {
			text-transform: uppercase;
			font-weight: 600;
			font-size: 14px;
			display: inline-block;
			margin-top: 5px;
		}
	}
	.author-add {
		font-size: 12px;
	}
	&.meta-1 {
		span {
			margin-right: 10px;
			i {
				margin-right: 3px;
			}
		}
	}
	&.meta-0 {
		span {
			padding: 4px 10px 4px 19px;
			font-size: 11px;
			letter-spacing: 0.8px;
			font-weight: bold;
			text-transform: uppercase;
			border-radius: 30px;
			position: relative;
			&::before {
				content: "";
				width: 6px;
				height: 6px;
				background: none;
				margin-right: 3px;
				border-radius: 5px;
				display: inline-block;
				position: absolute;
				top: 50%;
				left: 8px;
				margin-top: -3px;
				border: 1px solid $color-brand;
			}
		}
	}
}
span {
	&.has-dot {
		position: relative;
		padding-left: 10px;
		&::before {
			content: "";
			width: 4px;
			height: 4px;
			background: #999;
			border-radius: 50%;
			position: absolute;
			top: 50%;
			margin-top: -2px;
			display: block;
			left: -2px;
		}
	}
}
h6.post-title {
    font-size: 14px;
}
.post-title {
	a {
		color: $color-grey-1;
		&:hover {
			color: $color-brand;
		}
	}
}
.post-thumb {
    overflow: hidden;
	position: relative;
    &.border-radius-5 img{
        border-radius: 5px;
    }
    a {
        line-height: 1;
    }
	.entry-meta {
		position: absolute;
		top: 10px;
		left: 10px;
		z-index: 2;
		a {
			display: inline-block;
			color: #fff !important;
			background-color: $color-brand;
			border-radius: 30px;
			padding: 8px 16px;
		}
	}

}

/*Loop Grid*/
.loop-grid {
	position: relative;
	article {
		position: relative;
		box-shadow: 0 20px 50px rgba(0, 0, 0, 0.05);
		background: #fff;
		border-radius: 10px;
		overflow: hidden;
		border: 1px solid #eee;
	}
	.entry-content {
		padding: 30px;
	}
	.entry-content-2 {
		padding: 20px;
	}
	&.loop-list {
		position: relative;
		article {
			display: flex;
			align-items: center;
			.post-thumb {
				min-height: 280px;
				min-width: 350px;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center center;				
				overflow: hidden;
				-o-transition: all 0.4s ease;
				transition: all 0.4s ease;
				-webkit-transition: all 0.4s ease;
				-moz-transition: all 0.4s ease;
				-ms-transition: all 0.4s ease;				
			}
		}
	}
}
.img-hover-slide {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	min-height: 280px;
	overflow: hidden;
	-o-transition: all 0.4s ease;
	transition: all 0.4s ease;
	-webkit-transition: all 0.4s ease;
	-moz-transition: all 0.4s ease;
	-ms-transition: all 0.4s ease;
}
.top-right-icon {
	position: absolute;
	bottom: 15px;
	right: 15px;
	border-radius: 5px;
	width: 30px;
	height: 30px;
	text-align: center;
	line-height: 30px;
	z-index: 3;
	color: #fff;
	-o-transition: all 0.4s ease;
	transition: all 0.4s ease;
	-webkit-transition: all 0.4s ease;
	-moz-transition: all 0.4s ease;
	transition-duration: 0.4s;
	-ms-transition: all 0.4s ease;
}
.entry-meta {
	&.meta-1, &.meta-2 {
		display: flex;
		justify-content: space-between;
	}
	a.read-more {
		font-size: 12px;
		border-radius: 30px;
		border: 1px solid #abd7ab;
		padding: 10px 15px;
		color: #98ca98;
		font-weight: 600;
		display: inline-block;
	}
}